import { Maximize } from '@material-ui/icons';
import { useUserBiometrics } from '../hooks/useUserBiometrics';

export const useNutrientStatusValue = () => {
  const { biometrics, getBMRValue, getTEEValue } = useUserBiometrics();

  const { weight } = biometrics;

  const renderCaloricStatus = (currentValue) => {
    console.log(currentValue);

    if (currentValue > getTEEValue())
      return 'data-value__value--error data-value__value--extensive';
    if (currentValue < getBMRValue())
      return 'data-value__value--error data-value__value--deficite';
    return 'data-value__value--success';
  };

  const renderComplexNutrientStatus = (min, max, currentValue) => {
    //!check if correct for carbs
    let nutrientStatus = {
      minValue: weight.value * min,
      maxValue: weight.value * max,
      status: '',
    };

    nutrientStatus.status = 'data-value__value--success';
    if (currentValue > weight.value * max)
      nutrientStatus.status =
        'data-value__value--error data-value__value--extensive';
    if (currentValue < weight.value * min)
      nutrientStatus.status =
        'data-value__value--error data-value__value--deficite';

    return nutrientStatus;
  };

  const renderSimpleNutrientStatus = (
    minMen,
    maxMen,
    minWomen,
    maxWomen,
    currentValue
  ) => {
    //!check if correct for carbs
    const minValue = biometrics.gender === 'woman' ? minWomen : minMen;
    const maxValue = biometrics.gender === 'woman' ? maxWomen : maxMen;

    let nutrientStatus = {
      minValue: minValue,
      maxValue: maxValue,
      status: '',
    };

    nutrientStatus.status = 'data-value__value--success';
    if (currentValue > maxValue)
      nutrientStatus.status =
        'data-value__value--error data-value__value--extensive';
    if (currentValue < minValue)
      nutrientStatus.status =
        'data-value__value--error data-value__value--deficite';

    return nutrientStatus;
  };

  return {
    renderCaloricStatus,
    renderComplexNutrientStatus,
    renderSimpleNutrientStatus,
  };
};
